.gaugebar {
    width: 1336px;
    height: 122px;
    margin-top: 65px;
    border-radius: 60px;
    background-color: #fff;
    display: flex;
    align-items: center;
}
.innerGaugebar {
    height: 100px;
    border-radius: 50px;
    margin-left: 11px;
    /* background-image: linear-gradient(to right, #F7F6BB, #FFE23A); */
    transition: 0.2s;
}
.imgContainer {
    display: flex;
    justify-content: space-between;
    margin-top: -96px;  /* innerGaugebar 세로 중앙에 맞춤 */
    padding: 0 25px;
}